<template>
  <Form ref="formValidate" :model="formValidate" :label-width="80">
    <Row>
      <Col :span="7">
        <FormItem label="用户名" prop="name">
          <Input v-model="formValidate.name" placeholder="请输入用户名..."></Input>
        </FormItem>
      </Col>
      <Col :span="6">
        <FormItem label="用户状态" prop="accountStatus">
          <Select v-model="formValidate.accountStatus" placeholder="请选择用户状态..." filterable :clearable="true">
            <Option :value="item.value" v-for="item in options" :key="item.value">{{item.text}}</Option>
          </Select>
        </FormItem>
      </Col>
      <Col :span="6">
        <FormItem class="m-txt-right">
          <Button type="primary" @click="handleSubmit('formValidate')">确定</Button>
          <Button type="ghost" @click="handleReset" style="margin-left: 8px">重置</Button>
        </FormItem>
      </Col>
    </Row>
  </Form>
</template>
<script>
  export default {
    data() {
      return {
        formValidate: {
          name: '',
          accountStatus: ''
        },
        options: [{
          text: '启用',
          value: 1
        }, {
          text: '禁用',
          value: 2
        }]
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$emit('submitAdd', this.formValidate)
          }
        })
      },
      handleReset(name) {
        this.$refs['formValidate'].resetFields()
        this.$emit('submitAdd', this.formValidate)
      }
    }
  }
</script>
<style lang="scss" scoped>
  .m-txt-right {
    text-align: right;
  }
</style>



