<template>
  <div>
    <add-from :isAdd="isAdd" :modify="ismodify" @cancelAdd="cancelAdd" :formValidate="editList" @callback="callback" @submitAdd="loadListData"></add-from>
    <Table :columns="columns" :data="list" :loading="loading">
      <Button type="ghost" slot="header" class="m-marlr" @click="addRow">新增</Button>
    </Table>
  </div>
</template>

<script>
  import addFrom from './component/add.vue'
  import getListData from './mixins/getListData'
  import Cookies from 'js-cookie'
  export default {
    components: {
      addFrom
    },
    mixins: [getListData],
    created() {
      this.loadListData()
    },
    data() {
      return {
        canShowAdd: Cookies.get('administratorFlag'),
        isAdd: false,
        ismodify: false,
        editList: {
          name: '',
          phone: '',
          email: '',
          password: '',
          status: 1
        },
        list: [],
        loading: false,
        columns: [{
            title: '姓名',
            key: 'name',
            align: 'center'
          },
          {
            title: '角色',
            key: 'roleKey',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('span', params.row.roleKey === 'admin' ? (params.row.adminFlag === '1'?'超级管理员':'管理员') : (params.row.roleKey === 'dataEntry' ? '数据录入员' : (params.row.roleKey === 'review' ? '审核员': '')))
              ])
            }
          },
          {
            title: '区域',
            key: 'cityName',
            align: 'center'
          },
          {
            title: '电话',
            key: 'phone',
            align: 'center'
          },
          {
            title: '邮箱',
            key: 'email',
            align: 'center'
          },
          {
            title: '是否可管理账户',
            key: 'adminFlag',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('span', +params.row.adminFlag === 1 ? '可管理' : '不可管理')
              ])
            }
          },
          {
            title: '账号状态',
            key: 'status',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('span', +params.row.status === 1 ? '启用' : '禁用')
              ])
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            width: 250,
            render: (h, params) => {
              const modifyInfo = h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editRow(params)
                  }
                }
              }, '修改');
              const modifyMyInfo = h('Button', {
                props: {
                  type: 'ghost',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.$router.push('/info')
                  }
                }
              }, '移步个人中心');
              const deleteRow = h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.removeRow(params)
                  }
                }
              }, '删除')
              const resetPasswprd = h('Tooltip', {
                props: {
                  content: '初始化密码后，密码为123456',
                  theme: 'light',
                  placement: 'top-end'
                }
              }, [
                h('Button', {
                  props: {
                    // type: 'ghost',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.resetPasswordRow(params)
                    }
                  }
                }, '初始化密码')
              ])
              const child = JSON.parse(Cookies.get('Temp-User')).name === params.row.name ? [modifyMyInfo] : [modifyInfo, deleteRow, resetPasswprd]
              return h('div', child)
            }
          }
        ],
        params: {
          pageNumber: 1,
          pageSize: 10
        },
        combobox: [],
        url: {
          remove: ''
        }
      }
    },
    methods: {
      addRow() {
        this.isAdd = true
        this.ismodify = false
        this.editList = {
          name: '',
          phone: '',
          email: '',
          password: '',
          status: 1,
          cityId: '',
          roleId: ''
        }
      },
      // 修改
      async editRow(params) {
        this.isAdd = true
        this.ismodify = true
        const res = params.row
        this.editList = {
          id: res.id,
          name: res.name,
          phone: res.phone,
          email: res.email,
          status: Number(res.status),
          cityId: res.cityId,
          roleId: res.roleId
        }
      },
      // 删除
      removeRow(params) {
        this.$Modal.confirm({
          title: '提示',
          content: '<p>确定删除该管理员账户吗？</p>',
          loading: true,
          onOk: () => {
            this.$ajaxPost(this.projectName + '/staff/delete.do', {
              id: params.row.id
            }).then(response => {
              this.$Modal.remove()
              this.$Message.success('删除成功')
              this.loadListData()
            })
          }
        })
      },
      // 初始化密码
      resetPasswordRow(params) {
        this.$Modal.confirm({
          title: '提示',
          content: '<p>确定初始化该管理员账户的密码吗？</p>',
          loading: true,
          onOk: () => {
            this.$ajaxPost(this.projectName + '/staff/initPassword.do', {
              id: params.row.id
            }).then(response => {
              this.$Modal.remove()
              this.$Message.success(response)
              this.loadListData()
            })
          }
        })
      },
      loadListData(params) {
        this.params = params ? Object.assign({}, this.params, params) : this.params
        this.loading = true
        this.loadList(Object.assign({}, this.params)).then(res => {
          // console.log(res)
          this.loading = false
          this.list = res
        })
      },
      cancelAdd() {
        this.isAdd = false;
      },
      callback() {
        this.isAdd = true;
      }
    }
  }
</script>

<style>
  .page {
    float: right;
    margin: 20px 0;
  }
  .m-marlr {
    margin: 0 16px;
  }
</style>



