<template>
  <div>
    <query-form @submitAdd="loadListData"></query-form>
    <user-add :isAdd='isAdd' :formValidate="editList" :modify="ismodify" @cancelAdd="cancelAdd"
      @callback="callback" @submitAdd="loadListData" :options="options"></user-add>
    <Table :columns="columns" :data="list" :loading="loading">
      <Button type="ghost" slot="header" class="m-marlr" @click="addRow">新增</Button>
    </Table>
    <Page :total="total" show-total @on-change="pageChange" class="page"></Page>
  </div>
</template>

<script>
import userAdd from './component/user-add'
import queryForm from './component/query'
export default {
  components: {
    userAdd,
    queryForm
  },
  data() {
    return {
      list: [],
      loading: false,
      editList: {
        name: '',
        account: '',
        accountStatus: 1,
        phone: '',
        institution: '',
        job: '',
        password: '',
        email: '',
        allowReadIndexs: [],
        cityIds: []
      },
      isAdd: false,
      ismodify: false,
      total: 0,
      pageInfo: {
        pageNumber: 1,
        pageSize: 10
      },
      columns: [{
          title: '用户名',
          key: 'name',
          align: 'center'
        },
        {
          title: '用户账号',
          key: 'account',
          align: 'center'
        },
        {
          title: '用户状态',
          key: 'accountStatus',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('span', +params.row.accountStatus === 1 ? '启用' : '禁用')
            ])
          }
        },
        {
          title: '电话',
          key: 'phone',
          align: 'center'
        },
        {
          title: '邮箱',
          key: 'email',
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          align: 'center',
          render: (h, params) => {
            const modifyInfo = h('Button', {
              props: {
                type: 'primary',
                size: 'small'
              },
              style: {
                marginRight: '5px'
              },
              on: {
                click: () => {
                  this.editRow(params)
                }
              }
            }, '修改');
            const modifyMyInfo = h('Button', {
              props: {
                type: 'ghost',
                size: 'small'
              },
              style: {
                marginRight: '5px'
              },
              on: {
                click: () => {
                  this.$router.push('/info')
                }
              }
            }, '移步个人中心');
            const deleteRow = h('Button', {
              props: {
                type: 'error',
                size: 'small'
              },
              style: {
                marginRight: '5px'
              },
              on: {
                click: () => {
                  this.removeRow(params)
                }
              }
            }, '删除')
            const resetPasswprd = h('Tooltip', {
              props: {
                content: '初始化密码后，密码为123456',
                theme: 'light',
                placement: 'top-end'
              }
            }, [
              h('Button', {
                props: {
                  // type: 'ghost',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.resetPasswordRow(params)
                  }
                }
              }, '初始化密码')
            ])
            const child = [modifyInfo, deleteRow, resetPasswprd]
            return h('div', child)
          }
        }
      ],
      options: {
        allowReadIndexs1: [],
        cityIds1: []
      }
    }
  },
  created() {
    this.loadListData()
  },
  methods: {
    // 获取信息列表
    async loadListData(params) {
      this.params = params ? Object.assign({}, this.params, params) : this.params
      this.loading = true
      const res = await this.$ajaxPost(this.projectName + '/mgmt/account/finds.do', Object.assign({}, this.params, this.pageInfo))
      // console.log(res)
      this.loading = false
      this.list = res.rows
      this.total = res.total
    },
    // 新增用户
    addRow() {
      this.isAdd = true
      this.ismodify = false
      this.editList = {
        name: '',
        account: '',
        accountStatus: 1,
        phone: '',
        institution: '',
        job: '',
        password: '',
        email: '',
        allowReadIndexs: [],
        cityIds: []
      }
      this.options = {
        allowReadIndexs1: [],
        cityIds1: []
      }
    },
    // 修改信息
    editRow(params) {
      // console.log(params.row)
      const res = params.row
      this.isAdd = true
      this.ismodify = true
      this.editList = {
        id: res.id,
        name: res.name,
        account: res.account,
        institution: res.institution,
        job: res.job,
        accountStatus: Number(res.accountStatus),
        phone: res.phone,
        email: res.email,
        allowReadIndexs: [],
        cityIds: []
      }
      this.options.allowReadIndexs1 = res.allowReadIndexs.length !== 0 ? res.allowReadIndexs.map(item => item.id) : []
      this.options.cityIds1 = res.citys.length !== 0 ? res.citys.map(item => item.cityId) : []
    },
    // 删除信息
    removeRow(params) {
      this.$Modal.confirm({
        title: '提示',
        content: '<p>确定删除该用户吗？</p>',
        loading: true,
        onOk: () => {
          this.$ajaxPost(this.projectName + '/mgmt/account/delete.do', {
            id: params.row.id
          }).then(response => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadListData()
          })
        }
      })
    },
    // 初始化密码
    resetPasswordRow(params) {
      // console.log(params.row.id)
      this.$Modal.confirm({
        title: '提示',
        content: '<p>确定初始化该用户的密码吗？</p>',
        loading: true,
        onOk: () => {
          this.$ajaxPost(this.projectName + '/mgmt/account/initPassword.do', {
            id: params.row.id
          }).then(response => {
            this.$Modal.remove()
            this.$Message.success(response)
            this.loadListData()
          })
        }
      })
    },
    // 分页
    pageChange(val) {
      this.pageInfo.pageNumber = val
      this.loadListData()
    },
    cancelAdd() {
      this.isAdd = false
      this.options = {
        allowReadIndexs1: [],
        cityIds1: []
      }
      this.editList = {
        name: '',
        account: '',
        accountStatus: 1,
        phone: '',
        institution: '',
        job: '',
        password: '',
        email: '',
        allowReadIndexs: [],
        cityIds: []
      }
    },
    callback() {
      this.isAdd = true
    }
  }
}
</script>

<style>
  .page {
    float: right;
    margin: 20px 0;
  }
  .m-marlr {
    margin: 0 16px;
  }
</style>



